.input{
    width: 100%;
    box-sizing: border-box;
    padding: 10px;
    font-size: 20px;
    border: 1px solid rgb(0, 4, 49);
    margin-bottom: 20px;
    box-shadow: inset 2px 2px 8px rgba(75, 75, 75, 0.4);
}

.input:focus{
    outline: none;
    border: 1px solid rgb(255, 85, 0);
}